
//from 뒤에 라이브러리 명을 적으면 라이브러리 사용가능
import { createWebHistory, createRouter } from "vue-router";
import List from "@/components/List.vue";
import Home from "@/components/Home.vue";
import Detail from "@/components/Detail.vue";
import Author from "@/components/Author.vue";
import Comment from "@/components/Comment.vue";

/*router 선언 순서에 따라 우선 순위를 갖음*/
const routes = [
    {
        path: "/list",
        component: List,
    },
    {
        path: "/",
        component: Home,
    },
    {
        /*/경로/{id}/{param}*/
        /* 정규식 사용가는 {id}(\\d+) -> 숫자만*/
        /* 와일드카드 사용가능 {id}* (ex./{id}/{id}...) -> 숫자만*/
        path: "/detail/:id",
        component: Detail,
        name:'Detail',
        //true로 설정하면 데이터를 props로도 받음
        props: true,
        // nested routes 라우팅 페이지안의 페이지 (/detail/:id/author)
        children:[
            {
                path:"author",
                component: Author
            },
            {
                path: "comment",
                component: Comment
            }
        ]
    },

    //Navigation guards
    /*  특정 URL로 접속할 때 뭔가 코드를 실행하고 싶은 경우가 있습니다.
        예를 들어서 마이페이지를 만들었는데 이걸 로그인한 사람만 보여주고 싶은 경우
        라우팅해주기 전에 "니 로그인했냐" 이런 코드를 실행해야합니다.
        그럴때 navigation guard를 쓰면 됩니다.
        이것은 네비게이션해주기 전에 실행할 수 있는 코드, 그니까 hook 같은 거라고 보면 되겠습니다.*/
    /*{
        path: "/hello",
        component: HelloWorld,
        beforeEnter: ()=>{
            let 로그인했냐 = false;
        // /hello로 들어가기 전에 "로그인했냐라는 변수가 false면 /login 페이지로 보내기" 기능을 개발해놨습니다.
            if (로그인했냐 == false) {
                return '/login'
            }
        }
    },*/
];

//Hash Mode
const router = createRouter({
    history: createWebHistory(),
    routes,
});

//HTML5 Mode


    export default router;